import Vue from 'vue';
import Vuex, { ActionContext, ActionTree, GetterTree, Module, MutationTree } from 'vuex';
import { Contract, providers } from 'ethers';
import detectEthereumProvider from '@metamask/detect-provider';
import dayjs from 'dayjs';

import { abi } from '@/constants/BoardStaking.json';
// @ts-ignore
// import { staking as config } from '@/constants';
import { fromFloat2Integer, fromInteger2Float } from '@/libs';
import duration from 'dayjs/plugin/duration';

const ONE_YEAR = 60 * 60 * 24 * 365 / 3;
const HALF_YEAR = ONE_YEAR / 2;
// TODO: Move constants into constants file
const ADDR = '0x5612d570bd1ba1cc24bc86e4d448d8de985ba685';
dayjs.extend(duration);

Vue.use(Vuex);

export interface StakingState {
  countdown?: any;
  staked: string;
  profit: string;
  decimal: number;
  startBlock: number;
  minimumStaking: number;
  duration: number;
}

const getters: GetterTree<StakingState, any> = {
  getStaked(state: StakingState) {
    return fromInteger2Float(state.staked, state.decimal);
  },
  getProfit(state: StakingState) {
    return fromInteger2Float(state.profit, state.decimal);
  },
};

const mutations: MutationTree<StakingState> = {
  updateDuration(state: StakingState) {
    console.log('update duration')
    state.duration -= 1000;
    const duration = dayjs.duration(state.duration);
    const months = duration.months();
    const days = duration.days();
    const hours = duration.hours();
    const minutes = duration.minutes();
    const seconds = duration.seconds();
    state.countdown = {
      months,
      days,
      hours,
      minutes,
      seconds,
    };
  }
};

const provider = async (): Promise<any> => {
  return new providers.Web3Provider(await detectEthereumProvider() as any);
};

const signer = async (): Promise<any> => {
  return (await provider()).getSigner();
};

const actions: ActionTree<StakingState, any> = {
  calcCountdown(context: ActionContext<StakingState, any>) {
    const { state, rootState } = context;
    state.duration = (state.startBlock + HALF_YEAR - rootState.erc20.blockNumber) * 3000;
  },
  async getVNLAMinimalStaking(context: ActionContext<StakingState, any>) {
    console.log('minimal staking');
    const { state } = context;
    const ct = new Contract(ADDR, abi, await provider());
    state.minimumStaking = parseFloat(fromInteger2Float((await ct.getVNLAMinimumStaking()).toString(), state.decimal));
  },
  async withdrawVNLAProfit(context: ActionContext<StakingState, any>) {
    console.log('withdraw profit')
    const ct = new Contract(ADDR, abi, await signer());
    return (await ct.withdrawVNLAProfit()).toString();
  },
  async queryVNLAProfit(context: ActionContext<StakingState, any>) {
    console.log('query profit');
    const { state, rootState } = context;
    const ct = new Contract(ADDR, abi, await provider());
    state.profit = (await ct.queryVNLAProfit(rootState.wallet.accounts[0])).toString();
  },
  async queryVNLABalance(context: ActionContext<StakingState, any>) {
    console.log('query balance');
    const { state, rootState } = context;
    const ct = new Contract(ADDR, abi, await provider());
    state.staked = (await ct.queryVNLABalance(rootState.wallet.accounts[0])).toString();
  },
  async getStartBlock(context: ActionContext<StakingState, any>) {
    console.log('get start block');
    const { state } = context;
    const ct = new Contract(ADDR, abi, await provider());
    state.startBlock = parseInt((await ct.getStartBlock()).toString());
  },
  async stakeVNLA(context: ActionContext<StakingState, any>, amount: string) {
    console.log('stake vnla');
    const { state } = context;
    const ct = new Contract(ADDR, abi, await signer());
    return (await ct.stakeVNLA(fromFloat2Integer(amount, state.decimal)));
  },
  async withdrawVNLA(context: ActionContext<StakingState, any>, amount: string) {
    console.log('withdraw vnla');
    const { state } = context;
    const ct = new Contract(ADDR, abi, await signer());
    return (await ct.withdrawVNLA(fromFloat2Integer(amount, state.decimal)));
  },
};

export const staking: Module<StakingState, any> = {
  namespaced: true,
  state: {
    staked: '0',
    profit: '0',
    decimal: 18,
    startBlock: 7808888,
    minimumStaking: 50000,
    duration: Date.now(),
    countdown: {
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0,
      months: 0,
    },
  },
  getters,
  mutations,
  actions,
};
